import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home,
    },
    {
        path: '/Home',
        name: 'Home',
        component: Home,
    },
    {
        path: '/sobre-nosotros',
        name: 'About',
        component: () => import(/* webpackChunkName: "About" */ '../views/About.vue'),
    },
    {
        path: '/menu',
        name: 'Menu',
        component: () => import(/* webpackChunkName: "Menu" */ '../views/Menu.vue'),
    },
    {
        path: '/cafeterias',
        name: 'Cafeterias',
        component: () => import(/* webpackChunkName: "Cafeterias" */ '../views/Cafeterias.vue'),
    },
    {
        path: '/contacto',
        name: 'Contacto',
        component: () => import(/* webpackChunkName: "Contacto" */ '../views/Contacto.vue'),
    },
];

const router = new VueRouter({
    mode: 'history',
    base: '/practicawd/',
    routes,
});

export default router;
